
<template>
  <div class="space-y-4">
    <h1 class="text-xl font-semibold">Service Categories</h1>
    <div class="grid grid-cols-1 md:grid-cols-3 gap-4">
      <CategoryCard v-for="c in list" :key="c.id" :category="c"/>
    </div>
  </div>
</template>
<script setup lang="ts">
import CategoryCard from '@/components/CategoryCard.vue'
const { $get } = useApi()
const list = ref<any[]>([])
onMounted(async () => {
  const res:any = await $get('/service-categories?active=1')
  list.value = res.data || res
})
</script>
